<template>
  <div class="bgColor">
    <div class="container py-5">
      <!-- <h1 @click="addDatabase">1234</h1> -->
      <input type="file" id="testFile" @change="uploadFile" ref="files">
      <img v-if="fireImgUrl" :src="fireImgUrl" alt="img">
      <el-table
        id="out-table"
        :data="tableData"
        stripe
        style="width: 100%">
        <el-table-column
          prop="date"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="address"
          label="">
        </el-table-column>
      </el-table>
      <h2 @click="exportExecl">export Execl</h2>
    </div>
  </div>
</template>

<script>
import {db, storage} from '@/firebase.js'
import 'firebase/storage'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

  export default {
    data() {
      return {
        fireImgUrl: '',
        tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1517 '
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1519 '
        }, {
          date: '2016-05-03',
          name: '',
          address: ' 1516 '
        }],
      }
    },
    methods: {
      exportExecl () {  //  table  excel
        /* generate workbook object from table */
        let wb = XLSX.utils.table_to_book(document.querySelector('#out-table'))
        /* get binary string as output */
        let wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
        try {
        FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'sheetjs.xlsx')
        } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
        return wbout
      },
      next (snapshot) {  //  
        let percent = snapshot.bytesTransferred / snapshot.totalBytes * 100;
        console.log(percent + "% done");
      },
      completeUpload (uploadUrl) {  // 
        console.log('loading was completed !')
        storage.ref(uploadUrl).getDownloadURL().then(url => {
          this.fireImgUrl = url
        })
      },  
      uploadFile () {  // 
        const uploadedFile = this.$refs.files.files[0];
        console.log(uploadedFile)
        storage.ref('photo/' + uploadedFile.name).put(uploadedFile)
          .on('state_changed',this.next ,null, this.completeUpload('photo/' + uploadedFile.name))
      },
      addDatabase () { // DB  
        // db.ref('todos').once('value').then(result => {
        //   console.log(result.val())
        // })
        db.ref('huge-products').push({
          title: '3',
          code: 'A003',
          category : '',
          is_enabled: 'Y',
          description: '3',
        }).then(result => {
          console.log('')
        })
      },
    },
    mounted(){
      // storage.ref().child('BMI.png').getDownloadURL().then(url => {
      //   console.log(url)
      // })
      
    }
  }
</script>

